.auth{
    background: #000;
    background: #ccc ;
    display: grid;
    place-items: center;
    height: 100vh;
}

.form{
    background: white;
    border-radius: 10px;
    padding: 40px;
    display: flex;
    flex-direction: column;
    width: 400px;
}

.form input{
    padding: 15px 10px;
    border: none;
    border-bottom: 1px solid #000;
    margin-bottom: 20px;
    font-size: 16px;

}

.form a{
    display: inline-block;
    margin: 0 auto;
    text-decoration: underline;
    margin-bottom: 15px;
}

.form button{
    padding: 15px 0;
    background: #000;
    color: white;
    border-radius: 10px;
    font-size: 16px;
}

.header{
    height: 60px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px 0px 10px 0px #ccc;
    width: 100%;
}

.header a{
    color: #000;
    display: flex;
    align-items: center;
}

.header a span{
    margin-right: 10px;
}

.header button {
    font-size: 16px;
}

.main{
    display: flex;
    
}

.main_block{
    width: 100%;
}

.sidebar{
    transition: all .6s;
    width: 60px;
    height: 100vh;
    background: linear-gradient(120deg, #000 10%, #ccc 90%);
}

.hamburger{
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    row-gap: 5px;
    cursor: pointer;
}

.hamburger span{
    width: 50%;
    height: 4px;
    background: #fff;
}

.sidebar_link{
    display: none;
}

.sidebar_item a{
    color: #fff;
}

.sidebar_item {
    height: 60px;
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar.active{
    width: 300px;
}

.sidebar.active .sidebar_link{
    display: block;
    margin-left: 10px;
}

.sidebar.active .sidebar_item{
    width: 100%;
    justify-content: flex-start;
    padding-left: 17px;
}

.sidebar.active .sidebar_item a{
    display: flex;
}

.profile{
    padding: 30px;
    height: calc(100vh - 60px);
    overflow: scroll;
}

.profile_card{
    display: flex;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 9px 10px 0px #ccc;
}

.profile_card span{
    margin-right: 10px;
    font-size: 50px;
}

.profile_block {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 20px;
} 

.book_form{
    margin-top: 30px;
    box-shadow: 0px 0px 10px 0px #ccc;
}

.book_form textarea {
    padding: 15px 10px;
    margin-bottom: 10px;
    font-size: 16px;

}

.book_form h3{
    margin-bottom: 20px;
    text-align: center;
}

.books{
    padding: 30px;
    height: calc(100vh - 60px);
    overflow: scroll;
}
.books_block{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 20px;
    row-gap: 20px;
    margin-top: 30px;
}

.books_item {
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 10px 0px #ccc;
    background: #fff;
}

.books_item img{
    height: 100%;
    object-fit: cover;
    margin-right: 30px;
}

.books_text a{
    color: #000;
    display: inline-block;
    font-size: 25px;
    margin-bottom: 10px;
}

.books_text a:hover{
    text-decoration: underline;
}

.books_rating span{
    font-size: 24px;
    margin-right: 5px;
    color: rgb(231, 231, 16);
  }

.single{
    padding: 30px;
    height: calc(100vh - 60px);
    overflow: scroll;
}

.single_block{
    margin-top: 30px;
    display: flex;
}

.single_block img{
    margin-right: 30px;

}

.single_rating {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  
  .single_rating span {
    font-size: 20px;
    margin-right: 5px;
    color: #ccc;
  }

  .single_description {
      margin-top: 20px;
  }

  .single_authors{
      margin-top: 20px;
  }

  .single_date{
      margin-top: 20px;
  }

  .single h2{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .single h2 button{
    font-size: 18px;
    border-bottom: 1px solid #000;
    
  }

  @media screen and (max-width: 1300px) {
    .container {
      width: 100%;
      padding: 0 20px;
    }
    .books_block{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        margin-top: 30px;
    }
  }

  @media screen and (max-width: 1080px) {
    .books_block{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        margin-top: 30px;
    }
  }

  @media screen and (max-width: 760px) {
    .books_block{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        margin-top: 30px;
    }
  }
  
  @media screen and (max-width: 450px) {
    .sidebar.active{
        width: 150px;
        position: absolute;
    }   
  }